@import url(https://fonts.googleapis.com/css2?family=Inter&family=Manrope&family=Leckerli+One&display=swap);
@import url(https://realtechnerd.github.io/button.css/style.css);
.form-card {
    border-radius: 1rem !important;
    border: none !important;
    font-family: Inter;
}
.form-shadow {
    box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.1);
}
.log-in {
    color: #fff;
    font-family: Manrope;
    font-size: 15pt;
}
.btn-primary {
    border-radius: 1rem;
}
#title {
    color: #fff;
    font-family: Leckerli One;
    text-align: center;
}
.signup {
    height: 100vh;
    background: linear-gradient(90deg,#ff416c,#ff4b2b);
}
.login {
    height: 100vh;
    background: linear-gradient(90deg,#ff416c,#ff4b2b);
}
* {
    outline: none !important;
}
.Container {
    margin-left: 15% !important;
    margin-right: 15% !important;
}
@media only screen and (max-width: 600px) {
    .Container {
        margin-left: 10px !important;
        margin-right: 10px !important;
    }
}
#titlelink {
    text-decoration: none !important;
}
@media (prefers-color-scheme: dark) {
    body {
        background-color: #121212 !important;
    }
    .Task {
        background-color: #333 !important;
        color: #fff;
    }
    .Task:hover {
        background-color: #000 !important;
    }
    .dash-nav {
        background-color: #121212 !important;
    }
    .todo-form {
        background: #333 !important;
    }
    .todo-form input {
        background: #121212 !important;
        color: #fff;
    }
    .Loader {
        color: #fff !important;
    }
}
.Landing {
    height: calc(100vh - 62px);
    background: linear-gradient(90deg,#ff416c,#ff4b2b);
}
.landing-jumbotron {
    text-align: center;
    color: #fff;
    background-color: transparent !important;
    font-family: Manrope;
}
.display-4 {
    font-family: Leckerli One !important;
}
a.dash-brand {
    background: linear-gradient(90deg,#ff416c,#ff4b2b);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 18pt;
    font-family: Leckerli One;
}
.nav-shadow {
    box-shadow: 0 5px 15px rgba(56,56,56,.1);
}
.land-nav {
    font-family: Manrope !important;
}
.Task {
    background-color: #fff;
    padding: 5px;
    border-radius: 5px;
    margin-top: 7.5px;
    margin-bottom: 7.5px;
    cursor: pointer;
    transition: 0.2s;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: Manrope;
    box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.1);
}
.Task h2 {
    padding: 10px;
    font-size: 20pt;
    margin-top: 0px !important;
    margin-bottom: 0px !important;
}
.Task:hover {
    background-color: lightcoral;
    color: #fff;
    transform: scale(1.01);
}
.Task:active {
    transform: scale(0.99);
}
.Task p {
    padding: 10px;
    margin-bottom: 0px !important;
    margin-top: -14px !important;
}
.todo-form {
    padding: 10px;
    background: linear-gradient(90deg,#ff416c,#ff4b2b);
    text-align: center;
}
.todo-form input {
    width: 35.5%;
    border-radius: 5px;
    border: none;
    padding: 7.5px;
    font-size: 15pt;
    font-family: Manrope;
    transition: 0.2s;
}
.todo-form input:focus {
    box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.1);
}
@media only screen and (max-width: 600px) {
    .todo-form input{
        width: 100%;
    }
    #titleinput {
        margin-bottom: 10px;
    }
}
.Loader {
    height: calc(100vh - 129px) !important;
    color: #000;
}
.Loader {
    font-family: Manrope;
    text-align: center;
    padding: 5px;
}
.Loader img {
    width: 10%;
}
@media only screen and (max-width: 600px) {
    .Loader img {
        width: 50% !important;
    }
}
